// 安庆市第二人民医院 定制模块

import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {NgxQRCodeModule} from '@techiediaries/ngx-qrcode';

import {SharedModule} from 'app/shared/shared.module';
import {AqsdermyyRouter} from 'app/c-aqsdermyy/aqsdermyy-router';
import {OutpatientBillingComponent} from 'app/c-aqsdermyy/outpatient-billing/outpatient-billing.component';
import {PaymentComponent} from 'app/c-aqsdermyy/payment/payment.component';
import {PaymentResultComponent} from 'app/c-aqsdermyy/payment-result/payment-result.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    NgxQRCodeModule,
    AqsdermyyRouter
  ],
  declarations: [
    OutpatientBillingComponent,
    PaymentComponent,
    PaymentResultComponent
  ]
})
export class CAqsdermyyModule {

  constructor() {
  }
}



